<!--用户个人中心组件-->
<template>
  <div class="wow slideInLeft">
    <div class="block">
      <!-- 标题 -->
      <title-box title="用户个人中心"></title-box>
      <!-- 内容部分 -->
      <div>
        <el-row :gutter="20">
          <!-- 信息区 -->
          <el-col :span="12" :xs="24" :sm="12" >
            <el-form
                ref="ruleForm"
                :model="ruleForm"
                :rules="rules"
                label-width="120px"
                class="demo-ruleForm"
            >
              <el-form-item label="用户名"  >
                {{ruleForm.username}}
              </el-form-item>
              <el-form-item label="昵称" prop="nickname">
                <el-input v-model="ruleForm.nickname"></el-input>
              </el-form-item>
              <el-form-item label="邮箱">
                {{ruleForm.email}}
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">修改</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <!--头像-->
          <el-col :span="12" :xs="24" :sm="12" >
            <el-avatar shape="square"  :size="150" fit="cover" :src="ruleForm.avatar" ></el-avatar>
            <p>图片太大会无法上传哟</p>
            <el-upload
                action="http://39.105.29.9:9090/files/ossUpload"
                :limit="3"
                :on-success="uploadSuccess"
            >
              <el-button size="small" type="primary">点击修改头像</el-button>
            </el-upload>
          </el-col>
        </el-row>
      </div>
    </div>

  </div>
</template>

<script>
import WOW from "wowjs";
import TitleBox from '../components/TitleBox/titleBox'

export default {
  name: "UserIfo",
  components: {TitleBox},
  data() {
    return {
      ruleForm: {
        id:'',//用户id
        username: '',//用户名
        nickname:'',//昵称
        email:'',//邮箱
        avatar:'',//用户头像
      },
      currentNickname:'',//当前用户名
      rules: {
        nickname: [
          {required: true, message: '用户名不能为空', trigger: 'blur',},
          {min: 2, max: 7, message: '用户名长度要在2-7之间', trigger: 'blur',},
        ],
      },
    }
  },
//页面加载
  created() {
    this.loadUserInfo()
  },

//方法集合
  methods: {
    //上传成功时触发的方法(修改头像)
    uploadSuccess(response,file,fileList){
      this.ruleForm.avatar=file.response.data.url
      this.$http.post('/user/postUserById',this.ruleForm).then(res => {
        if(res.data.result){
          const user=JSON.parse(sessionStorage.getItem("user"))
          user.avatar=this.ruleForm.avatar
          //将用户信息存入浏览器
          sessionStorage.setItem("user",JSON.stringify(user))
          this.loadUserInfo()
        }else {
          this.$message({
            message: '服务器故障请稍后在修改',
            type: 'warning',
            offset:80
          });
        }

      })
    },
    //表单提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.currentNickname===this.ruleForm.nickname){
            this.$message({
              message: '请先做出改变才能进行修改',
              type: 'warning',
              offset:80
            });
          }else {
            this.$http.post('/user/postUserById',this.ruleForm).then(res => {
              if(res.data.result){
                const user=JSON.parse(sessionStorage.getItem("user"))
                user.nickname=this.ruleForm.nickname
                //将用户信息存入浏览器
                sessionStorage.setItem("user",JSON.stringify(user))
                this.loadUserInfo()
                this.$router.go(0)
                this.$message({
                  message: '修改成功',
                  type: 'success',
                  offset:80
                });
              }else {
                this.$message({
                  message: '服务器故障请稍后在修改',
                  type: 'warning',
                  offset:80
                });
              }

            })
          }
        } else {
          return false
        }
      })
    },
    //获取用户信息
    loadUserInfo(){
      const user=JSON.parse(sessionStorage.getItem("user"))
      if(user){
        this.ruleForm.id=user.id
        this.ruleForm.nickname=user.nickname
        this.ruleForm.email=user.email
        this.ruleForm.username=user.username
        this.currentNickname=user.nickname
        this.ruleForm.avatar=user.avatar

      }
    },
  },
  //可以访问DOM元素
  mounted() {
    //页面加载效果
    let wow = new WOW.WOW({
      boxClass: 'wow',
      animateClass: 'animated',
      offset: 0,
      mobile: true,
      live: true
    });
    wow.init();
  },
}
</script>

<style scoped>
/*弹出框图片展示div属性*/
.preview-image-wrap .preview-image{
    display: block;
    max-width: 100%;
    height: 200px;
}
</style>

